"use client"
import { useState } from "react"
import Image from "next/image"

interface StudentImageProps {
  sID: string
  fullname: string
  size?: number
  className?: string
}

export function StudentImage({
  sID,
  fullname,
  size = 60,
  className = "",
}: StudentImageProps) {
  const [error, setError] = useState(false)

  // üîπ ‡∏ñ‡πâ‡∏≤ error ‡πÉ‡∏´‡πâ‡πÉ‡∏ä‡πâ fallback ‡∏ó‡∏±‡∏ô‡∏ó‡∏µ
  const src = error
    ? "/img/student/00000.png"
    : `/img/student/${sID}.jpg`

  return (
    <Image
      key={src} // ‡πÉ‡∏´‡πâ re-render ‡πÄ‡∏°‡∏∑‡πà‡∏≠ src ‡πÄ‡∏õ‡∏•‡∏µ‡πà‡∏¢‡∏ô
      src={src}
      alt={fullname}
      width={size}
      height={size}
      className={`object-cover rounded-full border border-gray-300 dark:border-gray-600 ${className}`}
      onError={() => setError(true)} // ‚úÖ ‡πÄ‡∏°‡∏∑‡πà‡∏≠‡πÇ‡∏´‡∏•‡∏î‡πÑ‡∏°‡πà‡πÑ‡∏î‡πâ
    />
  )
}
